	onLoad() {
		uni.getSystemInfo({
			success: (res)=> {
				let height = res.windowHeight - uni.upx2px(115);
				this.swiperheight = height
			}
		});
	},
	methods: {
		
		back(){
			uni.navigateBack({
				delta: 1
			})
		},
		tabTabs(index){
			this.tabIndex=index;
		},
		toShop(){
			uni.navigateTo({
				url: '../../shop/shop',
			});
		},
		toProduct(e){
			uni.navigateTo({
				url:"../../product/product-show?id="+e.proid
			})
			
		},
		tabChange(e){
			uni.showLoading();
			this.tabIndex=e.detail.current;
			uni.hideLoading();
		},
		
	}
	
}
